@import '../../../assets/less/size.less';
@import '../../../assets/less/color.less';

// note: space-evenly does not work on Microsoft Edge Browser
.profile-page-wrapper {
    background-color: lightgray;
    height: @content-height;
    min-width: @app-min-width;
    display: flex;

    .ant-carousel {
        min-width: @app-min-width * 0.75;
        width: 100%;
        background: #e2e2e2;
        height: 100%;

        .slick-slider {
            height: 100%;
        }

        .slick-slide {
            text-align: center;
            background: rgb(250, 251, 252);
            overflow: auto;
        }

        .slick-slide h3 {
            color: #fff;
        }

        .slick-list {
            height: 100%;

            .slick-track {
                height: 100%;
            }
        }
    }
}